// Firebase 
const db = firebase.firestore();
db.collection('data').onSnapshot((snapshot) => {
    snapshot.docChanges().forEach((change) => {
        if (change.type === 'added') {
            console.log('New story: ', change.doc.data());
            bespoke.setGLTF(340, false,change.doc.data());
        }
        if (change.type === 'modified') {
            console.log('Modified story: ', change.doc.data());
            bespoke.setGLTF(1, true, change.doc.data());
        }
        if (change.type === 'removed') {
            console.log('Removed story: ', change.doc.data());
        }
    });
});

