// YUKA 
const vehicle = new YUKA.Vehicle();
vehicle.setRenderComponent(sphere, sync);

function sync(entity, renderComponent) {
    renderComponent.matrix.copy(entity.worldMatrix);
}

const wanderBehavior = new YUKA.WanderBehavior();
vehicle.steering.add(wanderBehavior);

// YUKA 
const time = new YUKA.Time();

// 
const floorTexture = textureLoader.load('/img/background.png'); //     
floorTexture.rotation = Math.PI / 2;
floorTexture.offset.x = -0; //   
floorTexture.offset.y = 1;
floorTexture.wrapS = THREE.MirroredRepeatWrapping;
floorTexture.wrapT = THREE.MirroredRepeatWrapping;
floorTexture.repeat.x = 0.5; //   2 

const floor = new THREE.Mesh(
    new THREE.PlaneGeometry(180, 100),
    new THREE.MeshStandardMaterial({
        color: '#F0EBEF',
        metalness: 0,
        roughness: 1,
        envMap: environmentMapTexture,
        envMapIntensity: 0.5,
        map: floorTexture
    })
);

floor.rotation.x = - Math.PI / 2;
floor.position.set(0, -5, 0);
floor.receiveShadow = true;
scene.add(floor);

//  
const handTexture = textureLoader.load('/img/hand1.svg');

const createHandMesh = (leftRight) => {
    const handMesh = new THREE.Mesh(
        new THREE.PlaneGeometry(4, 5.6),
        new THREE.MeshStandardMaterial({
            map: handTexture,
            transparent: true
        })
    );
    if(leftRight === 'right'){
        handMesh.scale.x = 1
    }else if(leftRight === 'left'){
        handMesh.scale.x = -1
    }
    handMesh.rotation.x = -Math.PI / 2;
    handMesh.position.set(0, -10, 0);
    handMesh.name = leftRight
    handMesh.visible = false;
    return handMesh;
};

const handMeshes = [createHandMesh('left'), createHandMesh('right')];

handMeshes.forEach(handMesh => {
    scene.add(handMesh);
});


