directionalLight.shadow.camera.right = 15 * 7;
directionalLight.shadow.camera.bottom = -15 * 7;
directionalLight.position.set(20, 40, -20);
directionalLight.shadow.bias = -0.001;
directionalLight.shadow.radius = 3;
directionalLight.rotation.z = -Math.PI * 0.2;
