//   
async function setupCamera() {
    const video = document.getElementById('video');
    video.width = sizes.width;
    video.height = sizes.height;

    const stream = await navigator.mediaDevices.getUserMedia({
        video: true
    });
    video.srcObject = stream;

    return new Promise((resolve) => {
        video.onloadedmetadata = () => {
            resolve(video);
        };
    });
}

const coordinate = {
    left:{
        x:null,
        y:null
    },
    right:{
        x:null,
        y:null
    }
}


async function detectHands(video) {
    const model = handPoseDetection.SupportedModels.MediaPipeHands;
    const detectorConfig = {
        runtime: 'mediapipe', //  'tfjs',
        solutionPath: 'https://cdn.jsdelivr.net/npm/@mediapipe/hands',
        modelType: 'lite'
    };
    const detector = await handPoseDetection.createDetector(model, detectorConfig);

    async function frame() {
        const hands = await detector.estimateHands(video);

        if (hands.length > 0) {
            const width = video.videoWidth;
            const height = video.videoHeight;

            rightHandMesh = handMeshes.find(mesh => mesh.name === 'right');
            leftHandMesh = handMeshes.find(mesh => mesh.name === 'left');

            //    
            let leftHandVisible = false;
            let rightHandVisible = false;

            hands.forEach((hand) => {
                let x = (((width - hand.keypoints[0].x) / 640) - 0.5) * 2;
                let y = ((hand.keypoints[0].y / 480) - 0.5) * 2;

                if(hand.handedness === 'Left'){
                    coordinate.left.x = x;
                    coordinate.left.y = y;
                    leftHandVisible = true;  //   
                } else if(hand.handedness === 'Right'){
                    coordinate.right.x = x;
                    coordinate.right.y = y;
                    rightHandVisible = true; //   
                }
            });

            //      
            if(rightHandMesh) {
                if (rightHandVisible) {
                    rightHandMesh.position.x = coordinate.right.x * 80;
                    rightHandMesh.position.z = coordinate.right.y * 35;
                }
                rightHandMesh.visible = rightHandVisible;
            }

            //      
            if(leftHandMesh) {
                if (leftHandVisible) {
                    leftHandMesh.position.x = coordinate.left.x * 80;
                    leftHandMesh.position.z = coordinate.left.y * 35;
                }
                leftHandMesh.visible = leftHandVisible;
            }
            
        } else {
            //       
            handMeshes.forEach(mesh => mesh.visible = false);
        }
        requestAnimationFrame(frame);
    }
    frame();
}

