import * as THREE from "../node_modules/three/build/three.module.js"
import { GLTFLoader, SkeletonUtils } from 'three/examples/jsm/Addons.js'
import * as YUKA from 'yuka'
import { gsap } from 'gsap';

export default class Bespoke {
    constructor(scene, entityManager, sync) {
        this.scene = scene;
        this.entityManager = entityManager;
        this.sync = sync;
        this.vehicleArray = [];
        this.colors = null;
        this.x = 0;
    }

    setGLTF(number, createNew, data){
        const gltfLoader = new GLTFLoader();

        const alignmentBehavior = new YUKA.AlignmentBehavior();
        alignmentBehavior.weight = 3.6;

        const cohesionBehavior = new YUKA.CohesionBehavior();
        cohesionBehavior.weight = 0.3;

        const separationBehavior = new YUKA.SeparationBehavior();
        separationBehavior.weight = 0.01;
        

        for(let i = 0; i < number; i++){
            const modelName = createNew ? data.modelName : this.random();
            const colorSet = createNew ? data.colors : this.randomColor();

            gltfLoader.load(
                `/Glb/${modelName}`,
                gltf => {

                    this.colors = colorSet;
                    const model = gltf.scene;

                    this.bespoke = SkeletonUtils.clone(model);
                    this.bespoke.matrixAutoUpdate = false;


                    this.bespoke.children[0].scale.set(0.12, 0.12, 0.12);
                    this.bespoke.children[0].rotation.z = 10 - Math.random() * 20;
                    this.bespoke.children[0].rotation.x = Math.PI;
                    this.bespoke.children[0].position.y = 8 - Math.random() * 8;
                    this.bespoke.children[0].castShadow = true;

                    this.bespoke.traverse((node) => {
                        if (node.isMesh === true) {
                            node.castShadow = true;
                            node.receiveShadow = true;
                        }
                    })

                    this.scene.add(this.bespoke);
                    this.bespoke.traverse((node) => {
                        if (node.name === 'change') {
                            node.children.forEach((item) => {
                                if(item.material && item.material.name == 'change_color1'){
                                    item.material = new THREE.MeshStandardMaterial({ color: this.colors[0] });
                                }else if(item.material && item.material.name == 'change_color2'){
                                    item.material = new THREE.MeshStandardMaterial({ color: this.colors[1] });
                                }else if(item.material && item.material.name == 'change_color3'){
                                    item.material = new THREE.MeshStandardMaterial({ color: this.colors[2] });   
                                }
                            });
                        }
                    });


                    this.vehicle = new YUKA.Vehicle();
                    this.vehicle.setRenderComponent(this.bespoke, this.sync);
                    this.vehicle.maxSpeed = 2.6;
                    this.vehicle.boundingRadius = 0.3;
        
                    const wanderBehavior = new YUKA.WanderBehavior();
                    wanderBehavior.weight = 0.5;
        
                    this.vehicle.updateNeighborhood = true;
                    this.vehicle.neighborhoodRadius = 10;
        
                    this.vehicle.steering.add(alignmentBehavior);
                    this.vehicle.steering.add(cohesionBehavior);
                    this.vehicle.steering.add(separationBehavior);
                    this.vehicle.steering.add(wanderBehavior);
        
                    if(createNew){
                        this.vehicle.position.x = 0;
                        this.vehicle.position.y = 130;
                        this.vehicle.position.z = 1;
                        this.vehicle.rotation.x = Math.PI * 0.5;
                    }else{
                        this.vehicle.position.x = 80 - Math.random() * 160;
                        this.vehicle.position.z = 20 - Math.random() * 40;
                        this.vehicle.rotation.fromEuler(0, 2.3 * Math.PI * Math.random() / 3, 0);
                    }
  
                    this.vehicleArray.push(this.vehicle);
                    this.entityManager.add(this.vehicle);

                    if(createNew){
                        setTimeout(() => {
                            this.animation();
                        }, 3000)
                    }else{}
                }
            );
        }
    }

    random(){
        this.nameRandom =  Math.floor(Math.random() * 4) + 1;
        this.modelName = `bespoke_${this.nameRandom}.glb`;
        return this.modelName;
    }

    randomColor() {
        const colorsArray = [
            ['#d5ebe5','#E2F2EF','#81C8A9'],
            ['#fffcf5','#D7C3A6','#6E7170'],
            ['#fff','#E5E6FA','#bce3cc'],
            ['#f2f1e9','#706e5e','#3e634d'],
            ['#fff','#3c595c','#a1a882'],
            ['#d0d1be','#d0d1be','#d0d1be'],
            ['#ffffff','#ffffff','#f2e6f0'],
            ['#ffffff','#f2e6f0','#f2e6f0'],
            ['#ffffff','#d996c9','#f2e6f0']
        ];
        
        const randomIndex = Math.floor(Math.random() * colorsArray.length);
        return colorsArray[randomIndex];
    }

    update(){
        this.x += 0.02;
        
        if(this.bespoke){
            for(let i = 0; i < this.vehicleArray.length; i++){
                let cosValue = Math.cos(this.x + i);

                // this.vehicleArray[i]._renderComponent.children[0].rotation.z += 0.01;
                this.vehicleArray[i]._renderComponent.children[0].rotation.z = cosValue /2;

                if(this.vehicleArray[i].position.x > 190 * 0.5){
                    this.vehicleArray[i].position.x = this.vehicleArray[i].position.x * -1 + 0.1;
                }else if(this.vehicleArray[i].position < -190 * 0.5){
                    this.vehicleArray[i].position.x = this.vehicleArray[i].position.x * -1 - 0.1;
                }
                if(this.vehicleArray[i].position.z > 110 * 0.5){
                    this.vehicleArray[i].position.z = this.vehicleArray[i].position.z * -1 + 0.1;
                }else if(this.vehicleArray[i].position.z < -110 * 0.5){
                    this.vehicleArray[i].position.z = this.vehicleArray[i].position.z * -1 - 0.1;
                }
            }
        }
    }

    animation(){
        const lastVehicle = this.vehicleArray[this.vehicleArray.length - 1];

        gsap.to(lastVehicle.rotation, {
            x: Math.PI * 0.5,
            y: 0,
            z: 0,
            duration: 2,
            ease: "ease-in-out"
        });

        gsap.to(lastVehicle.position, {
            x: 0,
            y: 0,
            z: 0,
            duration: 2,
            ease: "ease-in-out"
        });
    }
}
